'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { X, Users, Mail, Plus, Trash2 } from 'lucide-react'
import { Group } from './GroupPanel'

interface CreateGroupModalProps {
    isOpen: boolean
    onClose: () => void
    onCreateGroup: (group: Group) => void
}

export default function CreateGroupModal({ isOpen, onClose, onCreateGroup }: CreateGroupModalProps) {
    const [groupName, setGroupName] = useState('')
    const [groupDescription, setGroupDescription] = useState('')
    const [memberEmails, setMemberEmails] = useState<string[]>([''])
    const [loading, setLoading] = useState(false)
    const [errors, setErrors] = useState<{ [key: string]: string }>({})

    const handleAddEmailField = () => {
        setMemberEmails([...memberEmails, ''])
    }

    const handleRemoveEmailField = (index: number) => {
        if (memberEmails.length > 1) {
            setMemberEmails(memberEmails.filter((_, i) => i !== index))
        }
    }

    const handleEmailChange = (index: number, value: string) => {
        const newEmails = [...memberEmails]
        newEmails[index] = value
        setMemberEmails(newEmails)
    }

    const validateForm = () => {
        const newErrors: { [key: string]: string } = {}

        if (!groupName.trim()) {
            newErrors.groupName = 'Group name is required'
        }

        const validEmails = memberEmails.filter(email => email.trim())
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        
        validEmails.forEach((email, index) => {
            if (!emailRegex.test(email.trim())) {
                newErrors[`email_${index}`] = 'Please enter a valid email address'
            }
        })

        setErrors(newErrors)
        return Object.keys(newErrors).length === 0
    }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()
        
        if (!validateForm()) {
            return
        }

        setLoading(true)

        try {
            // Simulate API call
            await new Promise(resolve => setTimeout(resolve, 1500))

            const newGroup: Group = {
                id: Date.now().toString(),
                name: groupName.trim(),
                description: groupDescription.trim() || undefined,
                created_by: 'current_user_id',
                created_at: new Date().toISOString(),
                updated_at: new Date().toISOString(),
                member_count: memberEmails.filter(email => email.trim()).length + 1 // +1 for creator
            }

            onCreateGroup(newGroup)
            
            // Reset form
            setGroupName('')
            setGroupDescription('')
            setMemberEmails([''])
            setErrors({})
        } catch (error) {
            console.error('Error creating group:', error)
        } finally {
            setLoading(false)
        }
    }

    const handleClose = () => {
        if (!loading) {
            setGroupName('')
            setGroupDescription('')
            setMemberEmails([''])
            setErrors({})
            onClose()
        }
    }

    if (!isOpen) return null

    return (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4">
            <div className="bg-white rounded-2xl shadow-2xl max-w-md w-full max-h-[90vh] overflow-y-auto border border-gray-200">
                <div className="p-6">
                    {/* Header */}
                    <div className="flex items-center justify-between mb-6">
                        <div className="flex items-center space-x-3">
                            <div className="w-8 h-8 bg-gradient-to-br from-purple-400 to-purple-600 rounded-lg flex items-center justify-center">
                                <Users className="w-4 h-4 text-white" />
                            </div>
                            <h2 className="text-xl font-bold text-ocean-800">
                                Create New Group
                            </h2>
                        </div>
                        <Button
                            onClick={handleClose}
                            disabled={loading}
                            className="w-8 h-8 p-0 glass-button-sm"
                        >
                            <X className="w-4 h-4" />
                        </Button>
                    </div>

                    {/* Form */}
                    <form onSubmit={handleSubmit} className="space-y-4">
                        {/* Group Name */}
                        <div>
                            <label className="block text-sm font-medium text-ocean-700 mb-2">
                                Group Name *
                            </label>
                            <Input
                                type="text"
                                value={groupName}
                                onChange={(e) => setGroupName(e.target.value)}
                                placeholder="e.g., Family Health, Morning Routine"
                                disabled={loading}
                                className={errors.groupName ? 'border-red-300' : ''}
                            />
                            {errors.groupName && (
                                <p className="text-red-500 text-xs mt-1">{errors.groupName}</p>
                            )}
                        </div>

                        {/* Group Description */}
                        <div>
                            <label className="block text-sm font-medium text-ocean-700 mb-2">
                                Description (Optional)
                            </label>
                            <Textarea
                                value={groupDescription}
                                onChange={(e) => setGroupDescription(e.target.value)}
                                placeholder="What is this group about?"
                                disabled={loading}
                                rows={3}
                            />
                        </div>

                        {/* Member Emails */}
                        <div>
                            <label className="block text-sm font-medium text-ocean-700 mb-2">
                                Invite Members by Email
                            </label>
                            <div className="space-y-2">
                                {memberEmails.map((email, index) => (
                                    <div key={index} className="flex items-center space-x-2">
                                        <div className="flex-1">
                                            <Input
                                                type="email"
                                                value={email}
                                                onChange={(e) => handleEmailChange(index, e.target.value)}
                                                placeholder="member@example.com"
                                                disabled={loading}
                                                className={errors[`email_${index}`] ? 'border-red-300' : ''}
                                            />
                                            {errors[`email_${index}`] && (
                                                <p className="text-red-500 text-xs mt-1">
                                                    {errors[`email_${index}`]}
                                                </p>
                                            )}
                                        </div>
                                        {memberEmails.length > 1 && (
                                            <Button
                                                type="button"
                                                onClick={() => handleRemoveEmailField(index)}
                                                disabled={loading}
                                                className="w-8 h-8 p-0 bg-red-100 hover:bg-red-200 text-red-600"
                                            >
                                                <Trash2 className="w-3 h-3" />
                                            </Button>
                                        )}
                                    </div>
                                ))}
                                <Button
                                    type="button"
                                    onClick={handleAddEmailField}
                                    disabled={loading}
                                    className="w-full glass-button-sm flex items-center justify-center space-x-2"
                                >
                                    <Plus className="w-4 h-4" />
                                    <span>Add Another Email</span>
                                </Button>
                            </div>
                        </div>

                        {/* Submit Buttons */}
                        <div className="flex space-x-3 pt-4">
                            <Button
                                type="button"
                                onClick={handleClose}
                                disabled={loading}
                                className="flex-1 glass-button-secondary"
                            >
                                Cancel
                            </Button>
                            <Button
                                type="submit"
                                disabled={loading}
                                className="flex-1 glass-button flex items-center justify-center space-x-2"
                            >
                                {loading ? (
                                    <>
                                        <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                                        <span>Creating...</span>
                                    </>
                                ) : (
                                    <>
                                        <Users className="w-4 h-4" />
                                        <span>Create Group</span>
                                    </>
                                )}
                            </Button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
