'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { ArrowLeft, Plus, Users, Settings, UserCheck, UserX } from 'lucide-react'
import { Group } from './GroupPanel'
import GroupHabitList from './GroupHabitList'
import CreateGroupHabitModal from './CreateGroupHabitModal'

export interface GroupHabit {
    id: string
    group_id: string
    name: string
    description?: string
    color: string
    created_by: string
    created_at: string
    updated_at: string
}

export interface GroupMember {
    id: string
    user_id: string
    email: string
    display_name?: string
    role: 'admin' | 'member'
    joined_at: string
}

export interface GroupHabitCompletion {
    id: string
    group_habit_id: string
    user_id: string
    completion_date: string
    completed: boolean
    notes?: string
    completed_at: string
}

interface GroupViewProps {
    group: Group
    onBack: () => void
}

export default function GroupView({ group, onBack }: GroupViewProps) {
    const [groupHabits, setGroupHabits] = useState<GroupHabit[]>([])
    const [groupMembers, setGroupMembers] = useState<GroupMember[]>([])
    const [completions, setCompletions] = useState<GroupHabitCompletion[]>([])
    const [showCreateHabitModal, setShowCreateHabitModal] = useState(false)
    const [loading, setLoading] = useState(true)

    // Mock data for now - will be replaced with API calls
    useEffect(() => {
        setTimeout(() => {
            // Mock group habits
            setGroupHabits([
                {
                    id: '1',
                    group_id: group.id,
                    name: 'Take Morning Vitamins',
                    description: 'Daily vitamin supplements',
                    color: '#10B981',
                    created_by: 'user1',
                    created_at: new Date().toISOString(),
                    updated_at: new Date().toISOString()
                },
                {
                    id: '2',
                    group_id: group.id,
                    name: 'Morning Walk',
                    description: '30-minute walk together',
                    color: '#3B82F6',
                    created_by: 'user2',
                    created_at: new Date().toISOString(),
                    updated_at: new Date().toISOString()
                }
            ])

            // Mock group members
            setGroupMembers([
                {
                    id: '1',
                    user_id: 'user1',
                    email: 'you@example.com',
                    display_name: 'You',
                    role: 'admin',
                    joined_at: new Date().toISOString()
                },
                {
                    id: '2',
                    user_id: 'user2',
                    email: 'mom@example.com',
                    display_name: 'Mom',
                    role: 'member',
                    joined_at: new Date().toISOString()
                },
                {
                    id: '3',
                    user_id: 'user3',
                    email: 'dad@example.com',
                    display_name: 'Dad',
                    role: 'member',
                    joined_at: new Date().toISOString()
                }
            ])

            // Mock completions for today
            const today = new Date().toISOString().split('T')[0]
            setCompletions([
                {
                    id: '1',
                    group_habit_id: '1',
                    user_id: 'user1',
                    completion_date: today,
                    completed: true,
                    completed_at: new Date().toISOString()
                },
                {
                    id: '2',
                    group_habit_id: '1',
                    user_id: 'user3',
                    completion_date: today,
                    completed: true,
                    completed_at: new Date().toISOString()
                },
                {
                    id: '3',
                    group_habit_id: '2',
                    user_id: 'user1',
                    completion_date: today,
                    completed: true,
                    completed_at: new Date().toISOString()
                },
                {
                    id: '4',
                    group_habit_id: '2',
                    user_id: 'user2',
                    completion_date: today,
                    completed: true,
                    completed_at: new Date().toISOString()
                }
            ])

            setLoading(false)
        }, 1000)
    }, [group.id])

    const handleCreateHabit = (newHabit: GroupHabit) => {
        setGroupHabits(prev => [...prev, newHabit])
        setShowCreateHabitModal(false)
    }

    const handleToggleCompletion = (habitId: string, userId: string) => {
        const today = new Date().toISOString().split('T')[0]
        const existingCompletion = completions.find(
            c => c.group_habit_id === habitId && c.user_id === userId && c.completion_date === today
        )

        if (existingCompletion) {
            // Toggle existing completion
            setCompletions(prev => 
                prev.map(c => 
                    c.id === existingCompletion.id 
                        ? { ...c, completed: !c.completed, completed_at: new Date().toISOString() }
                        : c
                )
            )
        } else {
            // Create new completion
            const newCompletion: GroupHabitCompletion = {
                id: Date.now().toString(),
                group_habit_id: habitId,
                user_id: userId,
                completion_date: today,
                completed: true,
                completed_at: new Date().toISOString()
            }
            setCompletions(prev => [...prev, newCompletion])
        }
    }

    if (loading) {
        return (
            <div className="glass-card p-6">
                <div className="animate-pulse space-y-4">
                    <div className="h-4 bg-ocean-200 rounded w-3/4"></div>
                    <div className="h-4 bg-ocean-200 rounded w-1/2"></div>
                    <div className="h-4 bg-ocean-200 rounded w-2/3"></div>
                </div>
            </div>
        )
    }

    return (
        <div className="space-y-6">
            {/* Group Header */}
            <div className="glass-card p-6">
                <div className="flex items-center justify-between mb-4">
                    <Button
                        onClick={onBack}
                        className="glass-button-sm flex items-center space-x-2"
                    >
                        <ArrowLeft className="w-4 h-4" />
                        <span>Back to Groups</span>
                    </Button>
                    <Button className="glass-button-sm flex items-center space-x-2">
                        <Settings className="w-4 h-4" />
                        <span>Settings</span>
                    </Button>
                </div>

                <div className="flex items-center space-x-4 mb-4">
                    <div className="w-12 h-12 bg-gradient-to-br from-purple-400 to-purple-600 rounded-xl flex items-center justify-center">
                        <Users className="w-6 h-6 text-white" />
                    </div>
                    <div>
                        <h2 className="text-2xl font-bold text-ocean-800">
                            {group.name}
                        </h2>
                        {group.description && (
                            <p className="text-ocean-600">
                                {group.description}
                            </p>
                        )}
                    </div>
                </div>

                {/* Group Members */}
                <div className="flex items-center space-x-2 mb-4">
                    <Users className="w-4 h-4 text-ocean-600" />
                    <span className="text-sm text-ocean-600 font-medium">Members:</span>
                    <div className="flex items-center space-x-2">
                        {groupMembers.map((member, index) => (
                            <span key={member.id} className="text-sm text-ocean-700">
                                {member.display_name || member.email.split('@')[0]}
                                {index < groupMembers.length - 1 && ','}
                            </span>
                        ))}
                    </div>
                </div>
            </div>

            {/* Group Habits */}
            <div className="glass-card p-6">
                <div className="flex items-center justify-between mb-6">
                    <h3 className="text-lg font-semibold text-ocean-800">
                        Group Habits
                    </h3>
                    <Button
                        onClick={() => setShowCreateHabitModal(true)}
                        className="glass-button-sm flex items-center space-x-2"
                    >
                        <Plus className="w-4 h-4" />
                        <span>Add Habit</span>
                    </Button>
                </div>

                <GroupHabitList
                    habits={groupHabits}
                    members={groupMembers}
                    completions={completions}
                    onToggleCompletion={handleToggleCompletion}
                />
            </div>

            {/* Create Group Habit Modal */}
            <CreateGroupHabitModal
                isOpen={showCreateHabitModal}
                onClose={() => setShowCreateHabitModal(false)}
                onCreateHabit={handleCreateHabit}
                groupId={group.id}
            />
        </div>
    )
}
